@import '../../../../styles/Variables.module.scss';

.panel {
    height: auto;
    .content {
      padding: 0.75rem;
      .wrapper {
        padding: 0.75rem;
        border: 1px solid $divider;
        border-radius: $default-radius;
      }
      .row {
        padding: 0.5rem;
      }
      .header {
        text-transform: uppercase;
      }
    }
}

.timeline {
  max-width: 40rem;
}

.primaryTail {
  background-color: $main-primary !important;
}
.secondaryTail {
  background-color: $main-secondary !important
}

.tlnLeft {
  max-width: 11rem;
  .tlnOpsContent {
    padding-top: 0.2rem;
  }  
}

.linkCard {
  text-decoration: none;
  &:link {
    color: $common-black;
  }
}

.tlnContent {
  padding: 0.5rem 1rem 0.5rem 0.7rem;
  margin-bottom: 0.5rem;
  border-radius: $default-radius;
  box-shadow: 0 4px 6px -6px $common-black;
  background-color: $grey-100;
  // text-decoration: none !important;

  .picZone {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0 0 0.3rem;
  }
}
.tlnContentChild {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.1rem 0 0.1rem;
}
.tlnContentSuccess {
  @extend .tlnContent;
  border-left: solid 5px $main-success;
}
.tlnContentFailed {
  @extend .tlnContent;
  border-left: solid 5px $main-error;
}
.tlnContentPending {
  @extend .tlnContent;
  border-left: solid 5px $text-icon;
}

.chip {
  justify-self: flex-end;
  align-self: flex-start;
  margin-left: 0.5rem;
}
.chipInactive {
  color: $grey-400;
  font-style: italic;
  text-decoration: line-through $grey-400;
  opacity: 0.2 !important;
}
.chipSalesMoi {
  @extend .chip;
  background-color: $clarity !important;
  color: $common-white !important;
}
.chipSalesMoiInactive {
  @extend .chipSalesMoi;
  @extend .chipInactive;
}
