.avatar {
    width: 3.15rem !important;
    height: 4.2rem !important;
}
.salesmanName {
    padding-left: 0.5rem;
}
.weight {
    font-weight: 700 !important;
    color: #4caf50;
}

.KPIsZone {
    max-height: 56vh;
    overflow-y: auto;

    .cardKPIs {
        padding: 0.5rem 0;
        margin-bottom: 1rem;
        border: 1px solid $navy-100 !important;
        // &:hover {
        //     background-color: $grey-200 !important;
        //     cursor: pointer;
        // }
    
        .divider {
            border-right: 1px solid $grey-400 !important;
            text-align: center;
        }
        .noDivider {
            text-align: center;
        }
    
        .criName {
            font-weight: 500 !important;
            color: $dark-info !important;
            padding-left: 0.75rem;
        }
        .boxIcon {
            padding-right: 0.75rem;
            .txtRank {
                font-weight: 700;
            }
            .iconRank {
                width: 1rem;
                // height: 1.2rem;
                margin-left: 0.2rem;
            }
            .rankName {
                font-weight: 600;
                color: $dark-secondary;
                margin-left: 0.2rem;
            }
        }
    }
}