.btnSave {
    color: $main-info !important;
    &:hover {
        color: $dark-info !important;
    }
    &:disabled {
        color: $divider !important;
    }
}
.icon {
    float: right;
    padding: 0.5% !important;
}
.popover {
    padding: 0.3% !important;
}
.tBodyRow {
    cursor: pointer;
    text-decoration-line: line-through;
}

.title {
    font-weight: 600;
    color: $secondary-text;
    font-size: 1.2rem !important;
    padding: 1.6rem 0.5rem 0 0;
}

